import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
Habukhanothercharge.propTypes = {
  discount: PropTypes.object,
};
export default function Habukhanothercharge({ discount }) {
  const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
    bulk_sms: Yup.string().required(),
    affliate_price: Yup.string().required(),
    awuf_price: Yup.string().required(),
    agent_price: Yup.string().required(),
    monnify_charge: Yup.string().required(),
    paystack_charge: Yup.string().required(),
    xixapay_charge: Yup.string().required(),
    paymentpoint_charge: Yup.string().required(),
    bulk_length: Yup.string().required(),
    earning_min: Yup.string().required(),
    customer_amount: Yup.string().required(),
    vcard_ngn_fee: Yup.string().nullable(),
    vcard_usd_fee: Yup.string().nullable(),
    vcard_usd_rate: Yup.string().nullable(),
    vcard_ngn_fund_fee: Yup.string().nullable(),
    vcard_usd_fund_fee: Yup.string().nullable(),
    vcard_ngn_failed_fee: Yup.string().nullable(),
    vcard_usd_failed_fee: Yup.string().nullable(),
    vcard_fund_fee: Yup.string().nullable(),
  });

  const defaultValues = useMemo(
    () => ({
      bulk_sms: discount?.bulk_sms,
      affliate_price: discount?.affliate_price,
      awuf_price: discount?.awuf_price,
      agent_price: discount?.agent_price,
      monnify_charge: discount?.monnify_charge,
      paystack_charge: discount?.paystack_charge,
      xixapay_charge: discount?.xixapay_charge,
      paymentpoint_charge: discount?.paymentpoint_charge,
      bulk_length: discount?.bulk_length,
      earning_min: discount?.earning_min,
      customer_amount: discount?.customer_amount,
      vcard_ngn_fee: discount?.vcard_ngn_fee,
      vcard_usd_fee: discount?.vcard_usd_fee,
      vcard_usd_rate: discount?.vcard_usd_rate,
      vcard_ngn_fund_fee: discount?.vcard_ngn_fund_fee,
      vcard_usd_fund_fee: discount?.vcard_usd_fund_fee,
      vcard_ngn_failed_fee: discount?.vcard_ngn_failed_fee,
      vcard_usd_failed_fee: discount?.vcard_usd_failed_fee,
      vcard_fund_fee: discount?.vcard_fund_fee,
    }),
    [discount]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount !== undefined) {
      reset(defaultValues);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/other/charges/account/${AccessToken}/habukhan/secure`, data)
      swal('success', `Other Charges Updated Successfully`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField name="bulk_sms" label="Bulk SMS Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="bulk_length" label="Bulk SMS LENGTH (per page)" defaultValue="0" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="awuf_price" label="Awuf Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="agent_price" label="Agent Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="affliate_price" label="Affilate Website Price" defaultValue="0" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="monnify_charge" label="Monnify Charge (%)" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="paystack_charge" label="Paystack Charge (Amount)" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="xixapay_charge" label="Xixapay Charge (Amount)" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="paymentpoint_charge" label="PaymentPoint Charge (Amount)" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="earning_min" label="Earning Minimum Transfer" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="customer_amount" label="Customer Care Daily Amount" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />

              {/* <RHFTextField name="vcard_ngn_fee" label="V-Card NGN Creation Fee" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="vcard_usd_fee" label="V-Card USD Creation Fee" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="vcard_usd_rate" label="USD Exchange Rate (e.g. 1500)" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="vcard_ngn_fund_fee" label="NGN Card Funding Fee (%)" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="vcard_usd_fund_fee" label="USD Card Funding Fee ($)" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="vcard_ngn_failed_fee" label="NGN Failed Trans. Fee" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="vcard_usd_failed_fee" label="USD Failed Trans. Fee" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="vcard_fund_fee" label="General Funding Fee (%)" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} /> */}

            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
